import React from "react";

import LibrarySong from './LibrarySong';

const Library = ({ songs, setSongs, setCurrentSong, audioRef, isPlaying, libraryStatus }) => {
    return(
        <div className={`library ${libraryStatus ? "activeLibrary": ""}`}>
            <h2>Library</h2>
            <div className="library-songs">
                {
                    songs.map(song => (
                        <LibrarySong song={song} setSongs={setSongs} setCurrentSong={setCurrentSong} songs={songs} key={song.id} audioRef={audioRef} isPlaying={isPlaying} />
                    ))
                }
            </div>
        </div>
    )
}

export default Library;